<template>
  <div>
    <!-- 轮播图 -->
    <!-- van-swipe的高度要与图片高度相适应,才不会出现页面加载完成前出现空白区 -->
    <van-swipe :autoplay="2000" style="height:163.03px">
      <van-swipe-item v-for="(img,index) in bannerData" :key="index">
        <img v-lazy="img.bannerImg" preload class="banner-img" />
      </van-swipe-item>
    </van-swipe>

    <!-- 精品推荐 标识-->
    <van-row type="flex" justify="center" class="biaoshi">
      <van-col span="7">
        <van-icon name="qr" />
        <span class="title-biao">精品推荐</span>
      </van-col>
      <van-col span="15"></van-col>
      <van-col span="1">
        <van-icon name="arrow" />
      </van-col>
    </van-row>

    <!-- 精品推荐 商品-->
    <van-grid :border="false" :column-num="2">
      <van-grid-item v-for="(item,index) in recommentData" :key="index">
        <van-image radius="15" :src="item.largeImg" height="150" fit="cover" />
        <div class="recom-title">
          <span class="recom-name">{{item.name}}</span>
          <van-icon :name="item.isLike?'like':'like-o'" @click="inLike(item)" />
        </div>
      </van-grid-item>
    </van-grid>
  </div>
</template>

<script>
import "../../assets/css/commaint.less";
// 导入vuex相关文件
import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      // 轮播图数据
      bannerData: [],

      // 精品推荐数据
      recommentData: []
    };
  },

  created() {
    this.getBannerData();
    this.getRecommentData();
    this.findLike();
  },

  computed: {
    ...mapState(["bannerDataCache", "recommendDataCache"])
  },

  methods: {
    ...mapMutations(["changeData"]),

    // 获取轮播图数据
    getBannerData() {
      // 加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      // 判断是否发起请求数据
      if (this.bannerDataCache.length > 0) {
        this.$toast.clear();
        this.bannerData = this.bannerDataCache;
        return;
      }

      // 发起请求获取数据
      this.axios({
        method: "GET",
        url: "/banner",
        params: {
          appkey: this.appkey
        }
      })
        .then(res => {
          this.$toast.clear();

          if (res.status == 200) {
            this.bannerData = res.data.result;

            // 缓存数据
            this.changeData({ key: "bannerDataCache", value: res.data.result });
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },

    // 获取商品数据
    getRecommentData() {
      // 加载事件
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      if (this.recommendDataCache.length > 0) {
        this.$toast.clear();
        this.recommentData = this.recommendDataCache;
        return;
      }

      this.axios({
        method: "GET",
        url: "/typeProducts",
        params: {
          appkey: this.appkey,
          key: "isHot",
          value: 1
        }
      })
        .then(res => {
          if (res.data.code == 500) {
            res.data.result.forEach(v => {
              v.isLike = false;
            });
            this.$toast.clear();
            this.recommentData = res.data.result;

            // 缓存数据
            this.changeData({
              key: "recommendDataCache",
              value: res.data.result
            });
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },

    // 取消收藏-收藏商品
    inLike(item) {
      item.isLike = !item.isLike;
      let url = item.isLike ? "/like" : "/notlike";
      let tokenString = localStorage.getItem("__Tk");

      // 加载事件
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url,
        data: {
          appkey: this.appkey,
          pid: item.pid,
          tokenString
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 800 || res.data.code == 900) {
            this.isLike = !this.isLike;
            this.like = res.data.msg;
            this.$toast(this.like);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    // 查询收藏商品
    findLike() {
      let tokenString = localStorage.getItem("__Tk");
      // 加载事件
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findAllLike",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 2000) {
            res.data.result.forEach(p => {
              this.recommentData.forEach(v => {
                if (p.pid == v.pid) {
                  v.isLike = true;
                }
              });
            });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    }
  }
};
</script>

<style lang="less" scoped>
.banner-img {
  width: 100%;
}
// 标签
.biaoshi {
  height: 30px;
  font-size: 16px;
  position: relative;
  color: #2c1907;

  .title-biao {
    line-height: 30px;
    position: absolute;
    left: 35px;
  }
  .van-icon {
    font-size: 22px;
    color: #2c1907;
    line-height: 30px;
  }
}

// 商品

.recom-title {
  width: 100%;
  height: 15px;
  font-size: 16px;
  position: relative;
  .recom-name {
    color: #2c1907;
    position: absolute;
    left: 10px;
  }

  .van-icon {
    position: absolute;
    right: 10px;
    font-size: 20px;
    color: #2c1907;
  }
}
.van-grid {
  margin-bottom: 50px;
}
/deep/.van-grid-item__content {
  padding-top: 0;
}
</style>